/* 定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸 */
.Catalogue-pic::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #fff;
}
/* 定义滚动条轨道 内阴影+圆角 */
.Catalogue-pic::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #fff;
  background-color: #fff;
}
/* 定义滑块 内阴影+圆角 */
.Catalogue-pic::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px #DDDDDD;
  border-radius: 40px;
}
